@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.table-wrapper{max-width:600px;overflow:auto;height:100vh}.table-wrapper::-webkit-scrollbar{width:.5rem}.table-wrapper::-webkit-scrollbar-track{box-shadow:inset 0 0 6px rgba(0,0,0,.3)}.table-wrapper::-webkit-scrollbar-thumb{background-color:#a9a9a9;border-radius:10px}table{margin:1rem !important;border:none;margin-bottom:20px;width:100%;border-collapse:collapse}table thead th{position:-webkit-sticky;position:sticky;top:0;font-weight:bold;text-align:left;border:none;padding:10px 15px;background:#d8d8d8;font-size:14px;border-left:1px solid #ddd;border-right:1px solid #ddd}table tbody td{text-align:left;border-left:1px solid #ddd;border-right:1px solid #ddd;padding:10px 15px;font-size:14px;vertical-align:top}table thead tr th:first-child,table tbody tr td:first-child{border-left:none}table thead tr th:last-child,table tbody tr td:last-child{border-right:none}table tbody tr:nth-child(even){background:#f3f3f3}
.main{max-width:600px;overflow:auto}.main__action{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-around;justify-content:space-around}.main form{padding:1rem;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.main form .input{width:50%}.link{text-decoration:none}@media screen and (max-width: 600px){.main{max-width:100vw}}@media screen and (max-width: 320px){form{-webkit-flex-direction:column;flex-direction:column}}
body,body *{margin:0;padding:0;box-sizing:border-box;font-family:"Roboto",sans-serif}body{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}body::-webkit-scrollbar{width:.5rem}body::-webkit-scrollbar-track{box-shadow:inset 0 0 6px rgba(0,0,0,.3)}body::-webkit-scrollbar-thumb{background-color:#a9a9a9;border-radius:10px}.container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}
